'use client'

import React, { useEffect, useState, useCallback, useRef } from 'react'
import { useRouter } from 'next/navigation'
import { SidebarComponent } from '@/components/Sidebar/SidebarComponent'
import { PostSkeleton, StorySkeleton } from '@/components/Skeleton/Skeletons'
import { fetchAllPost } from '@/lib/api/postApi'
import { fetchUserInfo } from '@/lib/api/userApi'
import { fetchAllStories } from '@/lib/api/storyApi'
import { getToken, isAuthenticated } from '@/lib/utils/auth'
import { Post, Story, UserProfile, UserStory } from '@/types/api.types'

// Merge v√† sort theo th·ªùi gian
const mergeUniqueById = (prev: Post[], incoming: Post[]): Post[] => {
  const map = new Map<string, Post>()
  prev.forEach(p => {
    const id = p.id || ''
    map.set(id, p)
  })
  incoming.forEach(p => {
    const id = p.id || ''
    map.set(id, p)
  })
  return Array.from(map.values()).sort((a, b) => {
    const timeA = new Date(a.createdAt || 0).getTime()
    const timeB = new Date(b.createdAt || 0).getTime()
    return timeB - timeA
  })
}

export default function HomePage() {
  const [posts, setPosts] = useState<Post[]>([])
  const [user, setUser] = useState<UserProfile | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [stories, setStories] = useState<Story[]>([])
  const [isMobile, setIsMobile] = useState(false)
  const [sidebarOpen, setSidebarOpen] = useState(false)

  const [page, setPage] = useState(1)
  const [hasMore, setHasMore] = useState(true)
  const [isLoadingMore, setIsLoadingMore] = useState(false)

  const sentinelRef = useRef<HTMLDivElement | null>(null)
  const router = useRouter()

  // Check authentication
  useEffect(() => {
    if (typeof window === 'undefined') return
    if (!isAuthenticated()) {
      router.push('/login')
    }
  }, [router])

  // Check mobile
  useEffect(() => {
    if (typeof window === 'undefined') return
    const checkMobile = () => {
      setIsMobile(window.innerWidth < 768)
    }
    checkMobile()
    window.addEventListener('resize', checkMobile)
    return () => window.removeEventListener('resize', checkMobile)
  }, [])

  // Load user + stories
  const fetchData = useCallback(async () => {
    try {
      setIsLoading(true)
      const [userInfo, userStories] = await Promise.all([
        fetchUserInfo(),
        fetchAllStories(),
      ])
      setUser(userInfo)
      setStories(userStories || [])
    } catch (error) {
      console.error("Error fetching user/stories:", error)
    } finally {
      setIsLoading(false)
    }
  }, [])

  // Load posts
  const loadMorePosts = useCallback(async () => {
    if (isLoadingMore || !hasMore) return
    setIsLoadingMore(true)
    try {
      const result = await fetchAllPost(page, 5)
      const newPostsRaw = Array.isArray(result.posts) ? result.posts : []
      const hasNextPage = result.hasNextPage

      const newPosts = newPostsRaw.map((p: Post) => ({
        ...p,
        id: p.id,
        createdAt: p.createdAt || new Date().toISOString()
      }))

      setPosts(prev => mergeUniqueById(prev, newPosts))

      if (newPosts.length > 0) {
        setPage(prev => prev + 1)
      }
      setHasMore(Boolean(hasNextPage && newPosts.length > 0))
    } catch (error) {
      console.error("Error loading posts:", error)
    } finally {
      setIsLoadingMore(false)
    }
  }, [page, hasMore, isLoadingMore])

  // Initial load
  useEffect(() => {
    const token = getToken()
    if (!token) return

    fetchData()

    const loadInitialPosts = async () => {
      setIsLoadingMore(true)
      try {
        const result = await fetchAllPost(1, 5)
        const newPostsRaw = Array.isArray(result.posts) ? result.posts : []
        const hasNextPage = result.hasNextPage
        const first = newPostsRaw.map((p: Post) => ({
          ...p,
          id: p.id,
          createdAt: p.createdAt || new Date().toISOString()
        }))
        setPosts(first)
        setPage(2)
        setHasMore(Boolean(hasNextPage && first.length > 0))
      } catch (e) {
        console.error("Error loading first page:", e)
      } finally {
        setIsLoadingMore(false)
      }
    }

    loadInitialPosts()
  }, [fetchData])

  // Stories grouped by user
  const renderStories = () => {
    const storiesByUser: { [key: string]: Story[] } = {}

    if (Array.isArray(stories) && stories.length > 0) {
      stories.forEach(story => {
        if (!story.authorId) return
        if (!storiesByUser[story.authorId]) {
          storiesByUser[story.authorId] = []
        }
        storiesByUser[story.authorId].push(story)
      })
    }

    const usersWithStories: UserStory[] = Object.keys(storiesByUser).map(authorId => {
      const userStories = storiesByUser[authorId]
      userStories.sort((a, b) => {
        const timeA = a.timestamp || a.createdAt || ''
        const timeB = b.timestamp || b.createdAt || ''
        return new Date(timeB).getTime() - new Date(timeA).getTime()
      })

      return {
        authorId,
        stories: userStories,
        representativeStory: userStories[0]
      }
    })

    return (
      <div className="storyDiv bg-white rounded-2xl shadow-sm border border-gray-100 p-4 mb-6">
        <div className="flex space-x-3 overflow-x-auto pb-2 scrollbar-hide">
          {/* Add Story Button */}
          <div className="flex flex-col items-center gap-2 cursor-pointer">
            <div className="w-16 h-16 rounded-full bg-gradient-to-br from-sky-400 to-blue-500 flex items-center justify-center text-white text-2xl">
              +
            </div>
            <span className="text-xs text-gray-600">Add Story</span>
          </div>

          {isLoading ? (
            Array.from({ length: 5 }).map((_, index) => (
              <StorySkeleton key={`skeleton-${index}`} />
            ))
          ) : (
            usersWithStories.map((userStory) => (
              <div key={`story-${userStory.authorId}`} className="flex flex-col items-center gap-2 cursor-pointer">
                <div className="w-16 h-16 rounded-full bg-gradient-to-br from-pink-500 to-orange-400 p-0.5">
                  <img
                    src={userStory.representativeStory.mediaUrl || '/default-avatar.png'}
                    alt="Story"
                    className="w-full h-full rounded-full object-cover border-2 border-white"
                  />
                </div>
                <span className="text-xs text-gray-600 truncate max-w-16">
                  {userStory.representativeStory.firstName || 'User'}
                </span>
              </div>
            ))
          )}
        </div>
      </div>
    )
  }

  const renderPosts = () => (
    <div className="space-y-6 w-full">
      {isLoading ? (
        Array.from({ length: 3 }).map((_, i) => <PostSkeleton key={`post-skeleton-${i}`} />)
      ) : posts.length > 0 ? (
        <>
          {posts.map((post) => (
            <div key={post.id} className="bg-white rounded-2xl shadow-sm border border-gray-100 p-4">
              <div className="flex items-center gap-3 mb-4">
                <img
                  src={post.userImageUrl || "https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_640.png"}
                  alt="avatar"
                  className="w-12 h-12 rounded-full object-cover border-2 border-sky-200"
                />
                <div>
                  <p className="font-semibold text-gray-800">{post.firstName} {post.lastName}</p>
                  <p className="text-xs text-gray-500">{post.createdAt ? new Date(post.createdAt).toLocaleString() : ''}</p>
                </div>
              </div>

              {post.content && (
                <p className="text-gray-700 mb-4">{post.content}</p>
              )}

              {post.mediaUrls && post.mediaUrls.length > 0 && (
                <div className="rounded-xl overflow-hidden mb-4">
                  <img
                    src={post.mediaUrls[0]}
                    alt="Post media"
                    className="w-full h-auto object-cover"
                  />
                </div>
              )}

              <div className="flex gap-4 pt-2 border-t border-gray-100">
                <button className="flex items-center gap-2 text-gray-600 hover:text-red-500 transition-colors">
                  ‚ù§Ô∏è {post.likeCount || 0}
                </button>
                <button className="flex items-center gap-2 text-gray-600 hover:text-blue-500 transition-colors">
                  üí¨ {post.commentCount || 0}
                </button>
              </div>
            </div>
          ))}

          <div ref={sentinelRef} style={{ height: 1 }} />
        </>
      ) : (
        <div className="bg-white rounded-2xl shadow-sm border border-gray-100 p-12 text-center">
          <div className="w-24 h-24 mx-auto mb-6 rounded-full bg-gradient-to-br from-sky-100 to-blue-100 flex items-center justify-center">
            <svg className="w-12 h-12 text-sky-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2}
                d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2M5 11V9a2 2 0 012-2m0 0V5a2 2 0 012-2h6a2 2 0 012 2v2M7 7h10" />
            </svg>
          </div>
          <h3 className="text-xl font-bold text-gray-800 mb-2">No posts yet</h3>
          <p className="text-gray-500 text-sm">Start sharing your moments with friends!</p>
        </div>
      )}

      {isLoadingMore && (
        <div className="text-center text-gray-500 py-4">ƒêang t·∫£i th√™m...</div>
      )}
      {!hasMore && posts.length > 0 && (
        <div className="text-center text-gray-400 py-4">B·∫°n ƒë√£ xem h·∫øt b√†i vi·∫øt</div>
      )}
    </div>
  )

  return (
    <div className="flex min-h-screen">
      {/* Mobile menu button */}
      {isMobile && (
        <button
          onClick={() => setSidebarOpen(!sidebarOpen)}
          className="fixed top-4 left-4 z-50 p-2 bg-white rounded-md shadow-md border md:hidden"
          aria-label="Toggle menu"
        >
          <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            {sidebarOpen ? (
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            ) : (
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
            )}
          </svg>
        </button>
      )}

      {/* Mobile overlay */}
      {isMobile && sidebarOpen && (
        <div
          className="fixed inset-0 bg-black bg-opacity-50 z-40 md:hidden"
          onClick={() => setSidebarOpen(false)}
        />
      )}

      {/* Sidebar - Desktop */}
      <div className="hidden md:block w-[240px] h-screen bg-white border-r flex-shrink-0">
        <SidebarComponent />
      </div>

      {/* Sidebar - Mobile */}
      <div
        className={`
                    md:hidden fixed top-0 left-0 w-64 h-screen bg-white border-r z-50
                    transition-transform duration-300 ease-in-out
                    ${sidebarOpen ? 'translate-x-0' : '-translate-x-full'}
                `}
      >
        <SidebarComponent />
      </div>

      {/* Main Content */}
      <div className={`flex-1 min-h-screen w-full max-w-full overflow-x-hidden ${isMobile ? 'pt-16 px-4' : 'pl-3'}`}>
        <div className="min-h-screen bg-gradient-to-b from-gray-50 to-white">
          <div className="flex justify-center w-full px-4 xl:px-0 py-6">
            <div className="w-full max-w-[620px]">
              {renderStories()}

              {/* Create Post */}
              <div className="bg-white rounded-2xl shadow-sm border border-gray-100 p-4 mb-6">
                <div className="flex items-center gap-3">
                  <img
                    src={user?.imageUrl || "https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_640.png"}
                    alt="Your avatar"
                    className="w-12 h-12 rounded-full object-cover border-2 border-sky-200"
                  />
                  <button
                    className="flex-1 text-left px-4 py-3 bg-gray-50 hover:bg-gray-100 rounded-full text-gray-500 transition-colors duration-200"
                  >
                    What&apos;s on your mind, {user?.firstName || 'there'}?
                  </button>
                </div>
              </div>

              {renderPosts()}
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
